.doc {
	display: flex;
}

.doc .content {
	flex: 1;
	padding-left: 50px;
}

.doc li {
	width: 200px;
	padding-top: 10px;
}

* {
	list-style: none;
}

.doc ol {
	top: 100px;
	width: 200px;
	padding-left: 30px;
}

.doc ol li :hover{
	color: dimgray;
}

.left-blue {
	font-size: 18px;
	color: #000000;
	font-weight: 600;
	padding-left: 10px;
	border-left: 5px solid rgb(97, 218, 251);
}